import {
    ADD_PRODUCT
  } from '../types';

const initialState = {
    screams: []
  };

function dataReducer(state = initialState, action) {
    switch (action.type) {
        case ADD_PRODUCT:
          return {
            ...state,
            screams: action.payload
          };
        default:
            return state;
    }
}

export default dataReducer