import { ADD_PRODUCT } from '../types';
import axios from 'axios';

export const produxtAction = () => (dispatch) => {
    return new Promise((resolve, reject) => {
          const CORS_PROXY = "http://localhost:3001/";
        //   const CORS_PROXY = "https://cors-anywhere.herokuapp.com/";
          const url = `${CORS_PROXY}http://localhost:3000/url`;
          const config = {
              header: { 'content-type': 'aplication/json' }
          };
        axios
          .post(url, config)
          .then((res) => {
            console.log("all res Data",res)
            const categoyuData = res.data
            if (categoyuData) {
                dispatch({ type: ADD_PRODUCT, payload: categoyuData });
                resolve({ message: 'Success' });
            } else {
                reject({ message: 'Something fetch Category wrong!!' });
            }
           
          })
          .catch((err) => {
            reject({ message: 'Something went wrong!!' });
          });
        });
};



